const comfirmUi = {
  props: {
    comfirmContent: {
      type: [String]
    },
    data: {
      type: [Object]
    }
  },
  methods: {
    doIt (data) {
      this.$emit('increment', data);
    },
    cannelIt () {
      this.$emit('cannel');
    }
  }
}

export default comfirmUi;




